@charset "UTF-8";
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0; vertical-align: baseline; }

body, html { height: 100%; }

img, fieldset, a img { border: none; }

input[type="text"], input[type="email"], input[type="tel"], textarea { -webkit-appearance: none; }

textarea { overflow: auto; }

input, button { margin: 0; padding: 0; border: 0; }

input[type="submit"], button { cursor: pointer; }

div, input, textarea, select, button, h1, h2, h3, h4, h5, h6, a, span, a:focus { outline: none; }

ul, ol { list-style-type: none; }

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) { * { -webkit-text-size-adjust: none; } }
table { border-spacing: 0; border-collapse: collapse; width: 100%; }

html { box-sizing: border-box; }

*, *:before, *:after { box-sizing: inherit; }

@font-face { font-family: "svgfont"; src: url("fonts/svgfont.eot"); src: url("fonts/svgfont.eot?#iefix") format("eot"), url("fonts/svgfont.woff") format("woff"), url("fonts/svgfont.woff2") format("woff2"), url("fonts/svgfont.ttf") format("truetype"); font-weight: normal; font-style: normal; }
.icon, [class^="icon-"], [class*=" icon-"] { font-family: "svgfont"; font-style: normal; font-weight: normal; text-rendering: auto; speak: none; line-height: 1; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; }

.icon, [class^="icon-"], [class*=" icon-"] { display: inline-block; }

.icon-sepa:before { content: ""; }

.icon-skrill:before { content: ""; }

body { font-family: "Roboto", Arial, sans-serif; font-size: 16px; line-height: 1.3; font-weight: 700; }

html, body, .out { min-height: 100%; }

a { text-decoration: none; transition: all 0.3s ease; text-transform: uppercase; display: block; font: 700 16px/1 "Roboto", sans-serif, arial; }

b { font-weight: 700; }

img { max-width: 100%; display: block; margin: 0 auto; }

.title { font: 800 32px/1 "Cocomat", sans-serif, Arial; text-transform: uppercase; color: #242330; text-align: center; margin-bottom: 50px; }
.title span { color: #f77038; }
@media only screen and (max-width: 767px) { .title { font-size: 18px; margin-bottom: 25px; } }

.h3 { color: #242330; font-size: 20px; border: 10px solid #f63d3c; padding-left: 10px; }

.section__number { color: #f77038; font-family: Roboto; font-size: 98px; font-weight: 700; position: absolute; top: 40%; left: -25px; }

.btn { background-image: linear-gradient(to right, #f63d3c 0%, #f65639 48%, #f77138 100%); text-transform: uppercase; color: white; text-align: center; padding: 22px 50px; width: 270px; outline: 0; border: 0; transition: background-image 0.3s ease; }
.btn:hover { color: #343660; background-image: linear-gradient(to right, #93939c 0%, #93939c 48%, #93939c 100%); }
.btn:active { position: relative; top: -2px; }

.order-btn { color: white !important; outline: 0; width: 100%; background-image: linear-gradient(to right, #f63d3c 0%, #f65639 48%, #f77138 100%); }

.show-more { color: #f77038; font-size: 18px; color: #f77038; position: relative; display: inline-block; }
.show-more:after { content: ""; display: block; z-index: 1; position: absolute; height: 2px; bottom: -9px; width: 100%; transition: bottom 0.3s ease; background-image: linear-gradient(to right, #f63d3c 0%, #f65639 48%, #f77138 100%); }
.show-more:hover:after { bottom: 0px; }

.inner { width: 100%; max-width: 964px; margin: 0 auto; position: relative; *zoom: 1; }
.inner:after { content: " "; display: table; clear: both; }
@media only screen and (max-width: 1000px) { .inner { padding: 0 20px; } }
@media only screen and (max-width: 767px) { .inner { padding: 0; width: 90%; } }

.nav__list { font-size: 0; position: fixed; right: 30px; top: 50%; z-index: 200; }
@media only screen and (max-width: 767px) { .nav__list { right: 0; } }

.nav__list-item { margin-bottom: 13px; padding-left: 100px; }

.nav__list-link { color: #242330; font-weight: 300; position: relative; opacity: 0.3; transition: opacity 0.3s ease; transition: all 0.3s ease-out; }
.nav__list-link.is-active { color: #f77038; font-size: 24px; font-weight: 700; opacity: 1; text-indent: -7px; }
.nav__list-link.is-active:before { content: ""; display: block; z-index: 11; width: 164px; position: absolute; right: 49px; top: 50%; opacity: 1; height: 2px; background-image: linear-gradient(to top, #f63d3c 0%, #f65639 48%, #f77138 100%); }
@media only screen and (max-width: 767px) { .nav__list-link.is-active:before { width: 80px; } }

.main { min-height: 769px; background: url(../img/main-bg.png) center no-repeat; background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; opacity: 0.9; position: relative; }

.header { padding: 50px 0 0 0; *zoom: 1; }
.header:after { content: " "; display: table; clear: both; }
.header__logo { float: left; }
@media only screen and (max-width: 900px) { .header__logo { margin-bottom: 15px; } }
.header__logo a { font: 0/0 a; text-shadow: none; color: transparent; }
.header__info { float: right; }
.header__info a { font-weight: 300; font-size: 17px; color: white; position: relative; text-transform: none; max-width: 150px; text-align: center; line-height: 1.2; }
@media only screen and (max-width: 767px) { .header__info a { margin: auto; } }
.header__info a:after { content: ""; display: block; z-index: 1; position: absolute; width: 90%; margin: auto; right: 0; bottom: -9px; height: 2px; background-image: linear-gradient(to top, #f63d3c 0%, #f65639 48%, #f77138 100%); }
@media only screen and (max-width: 900px) { .header__info a:after { width: 100%; } }
@media only screen and (max-width: 900px) { .header__logo, .header__info { float: none; text-align: center; } }

.intro { color: white; padding-top: 80px; }
.intro p { font-weight: 300; font-size: 16px; }
.intro .title { text-align: left; color: white; }
.intro__content { max-width: 476px; }
.intro__text { margin-bottom: 44px; }
.intro__list { font-size: 0; margin-bottom: 46px; }

.intro__list-item { padding-left: 15px; position: relative; }
.intro__list-item:before { content: ""; display: block; background-image: linear-gradient(to top, #f63d3c 0%, #f65639 48%, #f77138 100%); width: 10px; height: 2px; position: absolute; top: 50%; left: 0; }
.intro__list-item:not(:last-child) { margin-bottom: 8px; }

.art { background: url(../img/item-bg.png) center no-repeat; background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; min-height: 2808px; z-index: -1; position: relative; top: -62px; }
.art .title { position: relative; top: 50px; }

.art__list { margin: 0 0 0 -2%; }

.art__list-item { width: 100%; display: table; }

.art__list-content, .art__list-layout { display: table-cell; vertical-align: top; }

.art__list-item:nth-child(odd) .art__list-content { padding-left: 30px; }

.art__list-item:nth-child(2n) .art__list-content { text-align: right; padding-right: 30px; }

.art__list-item:nth-child(2n) .art__list-title, .art__list-item:nth-child(2n) .art__list-text { padding-left: 0; padding-right: 20px; }

.art__list-item:nth-child(2n) .art__list-title:before { left: auto; right: 0; }

.art__list-content { padding-top: 30px; position: relative; }
.art__list-content:before { content: "01"; z-index: 1; display: block; position: absolute; top: 0; font-size: 98px; font-weight: 700; bottom: 0; max-width: 184px; left: 0; color: #f0f0f4; right: 0; margin: auto; text-align: center; opacity: 0.12; background-image: -webkit-linear-gradient(259deg, rgba(17, 23, 82, 0.7) 0%, transparent 100%); background-image: linear-gradient(191deg, rgba(17, 23, 82, 0.7) 0%, transparent 100%); }

.art__list-text { padding-left: 20px; color: #242330; font-weight: 300; }

.art__list-title { color: #242330; font-size: 20px; font-weight: 700; position: relative; padding-left: 20px; margin-bottom: 60px; }
.art__list-title:before { display: block; content: ""; z-index: 1; position: absolute; top: 0; left: 0; height: 100%; width: 3px; background-color: #f63d3c; }

.art__list-layout { position: relative; height: 240px; overflow: hidden; }
.art__list-layout img { width: 100%; height: 100%; }
.art__list-layout:after { content: ""; z-index: 1111; display: block; position: absolute; width: 100%; height: 100%; top: 0; left: 0; right: 0; bottom: 0; opacity: 0.9; background-image: linear-gradient(60deg, rgba(17, 23, 82, 0.7) 0%, transparent 100%); }

.unit { text-align: center; padding: 115px 0; position: relative; background: url(../img/unit-bg.png) center no-repeat; background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; }
.unit .subtitle { color: #242330; font-size: 20px; font-weight: 400; margin-bottom: 33px; }
@media only screen and (max-width: 767px) { .unit { padding: 45px 0; } }
.unit__list { float: left; font-size: 0; width: 32%; *zoom: 1; }
.unit__list:after { content: " "; display: table; clear: both; }
.unit__list:not(:last-child) { margin-right: 2%; }
@media only screen and (max-width: 767px) { .unit__list { width: 48%; }
  .unit__list:not(:last-child) { margin-bottom: 30px; } }
@media only screen and (max-width: 425px) { .unit__list { width: 98%; } }
.unit__wrapper { *zoom: 1; margin-bottom: 40px; }
.unit__wrapper:after { content: " "; display: table; clear: both; }

.unit__list-item { text-align: left; display: block; font: 700 16px/1 "Roboto", sans-serif, arial; color: black; }
.unit__list-item:not(:last-child) { margin-bottom: 24px; }
.unit__list-item:nth-of-type(3) .unit__list-pic { margin-bottom: 10px; }

.unit__list-title { font: 400 31px/1 "Cocomat", sans-serif, arial; color: #242330; margin-bottom: 24px; text-align: center; }

.unit__list-wrapper { padding: 24px 24px 0 25px; background-color: white; height: 325px; overflow: hidden; }

.unit__list-pic { height: 166px; position: relative; overflow: hidden; margin-bottom: 35px; }
@media only screen and (max-width: 767px) { .unit__list-pic { margin-bottom: 15px; } }
.unit__list-pic img { position: absolute; top: 0; left: 0; right: 0; bottom: 0; height: 100%; width: 100%; margin: auto; }

.unit__list-name { margin-bottom: 24px; font-size: 18px; }
@media only screen and (max-width: 767px) { .unit__list-name { margin-bottom: 15px; } }

.unit__list-content { *zoom: 1; margin-bottom: 36px; }
.unit__list-content:after { content: " "; display: table; clear: both; }

.unit__list-desc { float: left; color: #242330; font-weight: 300; line-height: 24px; }

.unit__list-price { float: right; font-size: 24px; color: #202351; line-height: 24px; }
@media only screen and (max-width: 900px) { .unit__list-price { font-size: 17px; } }

@media only screen and (max-width: 767px) { .unit__list-desc, .unit__list-price { float: none; } }

.unit__list-details { background-color: white; border-top: 1px solid #e1e1e1; *zoom: 1; }
.unit__list-details:after { content: " "; display: table; clear: both; }

.unit__list-col { float: left; height: 59px; width: 50%; }
.unit__list-col a { text-align: center; font-size: 0; display: block; font-size: 16px; height: 100%; color: #242330; }
.unit__list-col a:before { content: ""; display: inline-block; vertical-align: middle; width: 0; height: 100%; }


